import { Field, InputType } from "@nestjs/graphql";
import { IsNotEmpty, IsUUID } from "class-validator";
import { PaymentGatewayPlan } from "../../../../orm-config";
import { RelationExists } from "../../../../validation";


@InputType()
export class FindAllPaymentGatewayPlanInput {

  @Field(() => String)
  @IsNotEmpty()
  @RelationExists(PaymentGatewayPlan)
  @IsUUID(4)
  id: string;

}
